import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { FileTransfer} from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { LessonDetailPage } from '../lesson-detail/lesson-detail';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,
    private transfer: FileTransfer, private file: File) {
      
  }
  ionViewDidLoad(){
    this.readFile();
  }
  private fileTransfer = this.transfer.create();
  private lessons : Lesson[]
  downloadFile() {
    const url = 'https://www.dropbox.com/s/xntw7v7j60tnqrf/data.csv?dl=1';
    this.fileTransfer.download(url, this.file.dataDirectory + 'data.csv').then((entry) => {      
      this.readFile();
    }, (error) => {
      alert('download file error');      
    });
  }
  readFile(){
    this.file.readAsText(this.file.dataDirectory,'data.csv').then(results=>{      
      this.lessons = this.mapToLessons(results.split("\n")) 
      console.log(this.lessons);
    })
  }
  mapToLessons(rawDatas : string[]) : Lesson[]{
    var header = "";
    var headerIndex = 0;
    var results = [];
    var result;
    var newWord = "";
    for (var i = 0 ; i< rawDatas.length ; i++){
      var rawData = rawDatas[i];
      //console.log(rawData);
      if (rawData.includes("LessonTitle")){
         header =  rawData.split(": ")[1];
         if (result!=null){
          results.push(result)
         }         
         result = new Lesson(header);
         headerIndex = i ;
      }else{
        if (this.isMeaning(i - headerIndex)){
          result.addWord(new Word(newWord, rawData));
        }else{
          newWord = rawData; 
        }        
      }
    }    
    return results;   
  }
  isMeaning(index) : boolean{
    return (index%2 == 0)
  }
  
  onLessonClicked(lesson?: Lesson){
    this.navCtrl.push(LessonDetailPage,lesson);
  }
}
export class Lesson{
  name: string;
  words :Word[] = [];
  constructor(name: string){
    this.name = name;
    this.words = new Array();
  }
  addWord(word: Word){
    this.words.push(word);
  }
}
export class Word{
  name: string;
  meaning: string;
  constructor(name: string, meaning: string){
    this.name = name;
    this.meaning = meaning;
  }
}



